import React from "react";
import ListTableComponent from "../component/listTableComponent.jsx";
import { inject,observer } from "mobx-react";
import {post} from "../tool/http.js";
import ResetPassword from "../component/dialog/resetPassword.jsx";
const columnData = [
    // { id: 'name', numeric: true, disablePadding: false, label: '' },
    { id: 'email', numeric: true, disablePadding: false, label: '' },
    { id: 'money', numeric: true, disablePadding: false, label: '' },
    { id: 'clerk_name', numeric: true, disablePadding: false, label: '' },
    { id: 'status', numeric: true, disablePadding: false, label: '' },
    { id: 'created_at', numeric: true, disablePadding: false, label: '' },
    { id: 'updated_at', numeric: true, disablePadding: false, label: '' },
    { id: 'operat', numeric: true, disablePadding: false, extend: true, extendFn: (data) => {
        return new Promise((resolve,reject) => {
            post("business/pull_black",{
                status: 0,
                id: data.id
            }).then((res) => {
                if(res.data.code==1) {
                    alert("");
                    resolve(res.data);
                } else {
                    alert("");
                    resolve(res.data);
                }
            }).catch(reject);
        });
    },tipData: {
        title: "",
        content: ""
    } ,extendElement: (data) => {
        return <ResetPassword {...data} />;
    }, extendUrl: {
        title: "",
        link: "/tz_admin/business"
    }, label: '' }
];
@inject("clientelesStores")
@observer 
class ClienteleList extends React.Component {
    componentDidMount() {
        this.props.clientelesStores.getData();
    }
    updata() {
        this.props.clientelesStores.getData();
    }
    render() {
        return (
          <ListTableComponent 
            title="CRM"
            operattext=""
            headTitlesData={columnData} 
            data={this.props.clientelesStores.clienteles}  
            updata={this.updata.bind(this)}
          />
        );
      }
}
export default ClienteleList;